.focus, .focus__output {
    text-align: center;
}

h3, p {
    color: #ffffff;
    font-size: 2.5rem;
    font-weight: 400;
    margin-bottom: 1rem;
}

.focus__input {
    width: 100%;
    padding: 4px 2px 1px;
    display: block;
    border: 0;
    border-bottom: 2px solid #fff;
    color: #fff;
    font-size: 2.25em;
    line-height: 1.2;
    font-weight: 100;
    outline: none;
    text-align: center;
    transition: all .2s ease;
    -webkit-border-radius: 0;
    background-color: transparent;
}

.focus__output-text {
    font-style: italic;
}

