.links-heading {
    font-size: 1.3rem;
    display: inline-block;
    cursor: pointer;
}

.links__wrap {
    overflow-y: hidden;
    text-align: left;
    background: rgba(255,255,255,0.98);
    border-radius: 6px;
    color: #222;
    padding: 1rem;
    margin-top: 15px;
    max-height: 560px;
    max-width: 260px;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
}

.links__list {
    list-style: none;
}


.links__list-item {
    display: flex;
    flex-wrap: nowrap;
    white-space: nowrap;
    align-items: center;
    gap: 0.5rem;
    position: relative;
}


.links__list-item  .delete-icon {
    position: absolute;
    right: 0;
}