.todo__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    max-width: 300px;
    width: 100%;
}


.todo {
    overflow-y: hidden;
    text-align: left;
    background: rgba(255,255,255,0.98);
    color: #222;
    padding: 1rem;
    border-radius: 6px;
    /*min-height: 200px;*/
    max-height: 560px;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
}

.todo h1 {
    font-size: 1.5rem;
    font-weight: 100;
    margin-bottom: 0.5rem;
}

.todo-btn {
    color: #22222270;
    border: none;
    background: transparent;
}

.todo-btn:hover, .todo-btn:focus {
    color: #222;
}

.todo-row {
    color: #222;
    display: flex;
    justify-content: space-between;
    align-items: center;
    line-height: 2;
}

.todo__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow-y: scroll;
    max-height: 400px;
}


.todo-input {
    color: #222;
    background-color: transparent;
    width: 100%;
    padding: 1rem 1rem 1rem 0;
    display: block;
    border: none;
    font-size: 1rem;
    opacity: .75;
    overflow: hidden;
}

.todo-text__wrap {
    display: flex;
    align-items: center;
    gap: 0.4rem;
}

.todo-icons {
    justify-self: flex-end;
}

.todo-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.todo-onEdit {
    background: #222222;
}

.todo-text {
    cursor: pointer;
}

.todo-heading__wrap {
    display: flex;
    justify-content: space-between;
    align-content: center;
    padding: 0.5rem 0;
}

.todo-date {
    color: #222;
    font-weight: 400;
    font-size: 0.95rem;
}

.todo-date__wrap {
    display: flex;
    align-items: center;
    gap: 5px;
}

.react-calendar {
    display: none;

    padding-bottom: 7px;
}

.unfold-btn {
    height: 12px;
}

@media screen and (max-width: 767px) {
    .todo {
        order: 2;
    }
    .todo__wrap {
        max-width: 100%;
        align-items: initial;
    }
}