:root {
    --btn-animation: all 0.3s linear;
}

*, *::before, *::after {
    box-sizing: border-box;
}


html {
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-font-smoothing: antialiased;
    scroll-behavior: smooth
}

h1, h2, h3, h4, h5, p, ul {
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    font-family: 'Quicksand', sans-serif;
    color: white;
}

.app {
    position: relative;
    height: 100vh;
    width: 100%;
    margin: 0;
    padding: 20px;

    display: grid;
    grid-template-columns: 1fr 60% 1fr;
}

.first-cl {
    align-self: flex-start;
}

.secong-cl {
    align-self: center;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
}

.first-cl {
    align-self: flex-start;
}

.third-cl {
    align-self: flex-start;
}

.btn {
    cursor: pointer;
    transition: var(--btn-animation);
}

.btn:hover {
    opacity: 0.5;
}

.checked {
    text-decoration: line-through;
    opacity: 0.5;
}

input, button {
    outline: none;
}

svg {
    vertical-align: middle;
}

.visible {
    display: block;
}

@media screen and (max-width: 767px) {
    .app {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 40%;
    }
    .third-cl {
        align-self: flex-end;
    }
}